import React from "react";
import "./tough_roof.css";

const ToughRoof = ({ onOrderClick }) => {
  return (
    <div className="Tough_roof mrg-top">
      <div className="Tough-roof_special-container roof_special-container">
        <h1 className="Tough_roof-title  roof-title ">Жорстка покрівля</h1>
        <p className="Tough-roof_special-text roof_special-text">
          Жорстка покрівля – це поєднання довговічності та елегантного дизайну.
          Ми займаємося монтажем та ремонтом дахів з металочерепиці, шиферного
          покриття, керамічної або композитної черепиці. Кожна робота
          виконується з урахуванням характеристик матеріалу та особливостей
          вашого будинку. Жорстка покрівля забезпечує високий захист від
          зовнішніх впливів та підкреслює стиль вашої будівлі.
        </p>
        <div className="Tough_roof_bot-div roof_bot-div">
          <h3> Переваги:</h3>
          <ul className="special-text_list roof_special-text_list list">
            <li className="special-text_item">
              Довговічність покриття – термін служби до 50 років.
            </li>
            <li className="special-text_item">
              Висока вогнестійкість, що робить її безпечною.
            </li>
            <li className="special-text_item">
              Стійкість до снігових та вітрових навантажень.
            </li>
          </ul>
        </div>
        <div
          className="Soft-roof_btn-container btn-special_container"
          onClick={onOrderClick}
        >
          <div className="feedback-container">
            <div className="feedback-border">
              <div className="feedback">
                <div className="feedback-text-container">
                  <p className="feedback-text">Замовити дзвінок</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <img
        className="Tough-roof_special-img roof_special-img"
        src="./img/tough_roof.jpg"
        alt=""
      ></img>
    </div>
  );
};

export default ToughRoof;
